import React, { Component } from 'react';
import Logo from './Logo';
import CenturyClub from './CenturyClub';
import './App.css';

class App extends Component {
  render() {
    return (
      <div className="App">
        <div className="App-header">
          <Logo />
          <h2>Century Club</h2>
        </div>
        <CenturyClub />
      </div>
    );
  }
}

export default App;



